import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import { useEffect, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  useFinanceAllowPaymentInstallment,
  useFinanceInvoicePatternSetting,
  useFinanceShowReceiptType,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

import Switch from "@mui/material/Switch";
const label = { inputProps: { "aria-label": "Switch demo" } };

const FinanceSetting = ({ masterData, fid, instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    show_receipt: "Normal",
    online_amount_edit_access: false,
    show_invoice_pattern: "",
  });

  const [financeShowReceiptType] = useFinanceShowReceiptType();
  const [financeAllowPaymentInstallment] = useFinanceAllowPaymentInstallment();
  const [financeInvoicePatternSetting] = useFinanceInvoicePatternSetting();

  useEffect(() => {
    if (masterData?.show_receipt) {
      setState((prev) => ({
        ...prev,
        show_receipt: masterData?.show_receipt,
      }));
    }
    if (masterData?.online_amount_edit_access) {
      setState((prev) => ({
        ...prev,
        online_amount_edit_access:
          masterData?.online_amount_edit_access === "Granted" ? true : false,
      }));
    }
    if (masterData?.show_invoice_pattern) {
      setState((prev) => ({
        ...prev,
        show_invoice_pattern:
          masterData?.show_invoice_pattern === "Institute_Wise"
            ? "Institute Wise"
            : "Bank Wise",
      }));
    }
  }, [
    masterData?.show_receipt,
    masterData?.online_amount_edit_access,
    masterData?.show_invoice_pattern,
  ]);
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  // console.info("masterData", masterData);
  const onSave = () => {
    if (fid) {
      setDisabled((pre) => !pre);
      financeShowReceiptType({
        fid: fid,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onAllowPaymentInstallment = (e) => {
    if (instituteId) {
      setState((prev) => ({
        ...prev,
        online_amount_edit_access: e.target.checked,
      }));
      setDisabled((pre) => !pre);
      financeAllowPaymentInstallment({
        flow: "Institute_Admin",
        bodyParameter: {
          id: instituteId,
          sid: null,
          online_amount_edit_access: e.target.checked
            ? "Granted"
            : "Not Granted",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info("masterData", masterData);
  const onInvoicePatternSave = () => {
    if (fid && state.show_invoice_pattern) {
      setDisabled((pre) => !pre);
      financeInvoicePatternSetting({
        fid: fid,
        bodyParameter: {
          show_invoice_pattern:
            state.show_invoice_pattern === "Institute Wise"
              ? "Institute_Wise"
              : "Bank_Wise",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  //   console.info("masterData", masterData);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("setting")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <div
          className={style.fm_header_container}
          style={{
            gap: "2%",
            alignItems: "flex-end",
          }}
        >
          <CustomSelect
            selectLabel={t("admission_receipt_type")}
            selectedValue={t("please_select_receipt_type")}
            defalutValue={state.show_receipt}
            onClick={(val) => onSelectDropdown(val, "show_receipt")}
            options={["Normal", "Society"]}
            customStyleContainer={{
              width: "49%",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "1",
            }}
          />
          <CreateButton
            label="save"
            onAction={onSave}
            customStyle={{
              width: "49%",
              margin: "0",
              padding: "0.5rem 1rem",
            }}
          />
        </div>
        <BorderBottom />

        <div>
          <Switch
            {...label}
            checked={state.online_amount_edit_access}
            onChange={onAllowPaymentInstallment}
          />
          <span>{t("online_install_edit_fees")}</span>
        </div>
        <BorderBottom />

        <div
          className={style.fm_header_container}
          style={{
            gap: "2%",
            alignItems: "flex-end",
          }}
        >
          <CustomSelect
            selectLabel={t("invoice_viewer")}
            selectedValue={t("please_select_invoice_viewer")}
            defalutValue={state.show_invoice_pattern}
            onClick={(val) => onSelectDropdown(val, "show_invoice_pattern")}
            options={["Institute Wise", "Bank Wise"]}
            customStyleContainer={{
              width: "49%",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "1",
            }}
          />
          <CreateButton
            label="save"
            onAction={onInvoicePatternSave}
            customStyle={{
              width: "49%",
              margin: "0",
              padding: "0.5rem 1rem",
            }}
          />
        </div>
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default FinanceSetting;
